<template>
  <div class="content">
    <section class="feedContent">
      <div class="topContent">
        <div class="title">
          <font color="red">*</font>请描述你遇到的问题类型
        </div>
        <div class="btnContent">
          <div v-for="(item) in btnItem" :key="item.id" @click="clectBtn(item.id)">
            <div :class="['btn',item.active?'active':'true']">{{item.title}}</div>
          </div>
        </div>
      </div>
      <div class="submitContent">
        <div class="title">
          <font color="red">*</font>请文字描述你的问题
        </div>
        <van-cell-group>
          <van-field
            v-model="message"
            rows="2"
            autosize
            type="textarea"
            maxlength="200"
            placeholder="请描述问题"
            show-word-limit
          />
        </van-cell-group>
      </div>
      <div class="submitContent">
        <div class="title">
          <font color="red">*</font>请输入您反馈的手机号
        </div>
        <van-cell-group>
          <van-field v-model="value" placeholder="请输入手机号" type="number" />
        </van-cell-group>
      </div>
    </section>
    <div class="bottomBtn">
      <van-button
        type="primary"
        size="large"
        color="linear-gradient(to right, #4bb0ff, #6149f6)"
        to="/"
      >提交反馈</van-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      btnItem: [
        { title: "绑卡问题", id: 1, active: false },
        { title: "身份验证", id: 2, active: false },
        { title: "款项问题", id: 3, active: false },
        { title: "绑卡问题", id: 4, active: false }
      ],
      message: "",
      value: ""
    };
  },
  methods: {
    clectBtn(id) {
      var self = this;
      var json = self.btnItem;
      json.forEach(el => {
        if (id == el.id) {
          el.active = !el.active;
        }
      });
      self.btnItem = json;
    }
  }
};
</script>

<style lang="scss" scoped>
.feedContent {
  padding: 0.8rem;
  .title {
    @include sc(0.9rem, $fColor);
    @include wh(100%, auto);
    font-weight: bold;
    text-align: left;
    margin: 1rem 0rem;
  }
  .topContent {
    @include fc(flex-start);
    .btnContent {
      @include fw(space-around);
      @include wh(100%, auto);
      .btn {
        padding: 0.5rem;
        @include sc(0.8rem, $grayfont);
        background-color: $Tgray;
        border-radius: 1rem;
      }
      .active {
        @include sc(0.8rem, $fc);
        background-color: $blue;
      }
    }
  }
}
.bottomBtn {
  position: fixed;
  top: 50%;
  @include wh(100%, auto);
  padding: 0rem 0.8rem;
}
</style>